import util from '../util/base_util.js'

let d3 = require('d3');
let selectById = function (id) {
    //
    let model = util.getContainerModel(id);
    //svg
    let addSvg = util.selectContainerAddSvg()
        .id(id)
        .model(model);
    let svg = addSvg();
    return {
        draw(data, config) {
            //d3hierarchychildrenchildren
            let dataString=JSON.stringify(data);
            let hierarchy=config.VC.hierarchy;
            hierarchy.forEach(item=>{
                let reg='\\b'+item+'\\b';
                dataString= dataString.replace(new RegExp(reg,'g'),'children');
            });
            data=JSON.parse(dataString);
            // console.log(data);
            //,g
            let tx = model.tx;
            let ty = model.ty;
            let gW = model.gW;
            let gH = model.gH;
            //g
            //g0,0
            let g = svg.append('g').attr('transform', 'translate(' + tx + ',' + ty + ')');
            //d3pack
            let newData1 = d3.hierarchy(data, function (d) { //root=newData
                return !config.VC?d.children:d[config.VC.children] || d.children //children
            });
            //valuecountsortsumvalue
            //

            newData1.count();//
            //
            let partitionData = d3.partition()
                .size([gW, gH])
                .padding(1);  //

            //
            let newData = partitionData(newData1).descendants();//partitionData(data)
            //console.log(newData);
            /*
                        :
                        x: 
                        y: 
                        dx: 
                        dy: 
                        depth: ()
                        value: 
            */
            let getColor = d3.scaleOrdinal(d3.schemeCategory10);
            //
            let rectGs = g.selectAll('g.rectPartition')
                .data(newData)
                .enter()
                .append('g')
                .attr('class', 'rectPartition');
            let gg = rectGs.append('rect')
                .attr('x', function (d) {
                    return d.x0
                })
                .attr('y', function (d) {
                    return d.y0
                })
                .attr('width', function (d) {
                    return d.x1-d.x0
                })
                .attr('height', function (d) {
                    return d.y1-d.y0
                })
                .attr('fill', function (d) {
                    return getColor((d.children ? d : d.parent).data.name)//
                });
            //
            if (config && 'text' in config) {
                let add= util.setMultiText()
                    .content(config.text.content)
                    .posX(function (d) {
                        return d.x0
                    })
                    .posY(function (d) {
                        return d.y0
                    })
                    .rowMaxWidth(function (d) {
                        return d.x1-d.x0
                    })
                    .fontSize(config.text.fontSize);
                add(gg);
                d3.selectAll('.multiText')
                    .style('text-anchor','start');
            }

        }
    }
};

export default {
    selectById
}