<script>

    export default {
        name: "Basic_bar",
        methods: {
            //cdview
            draw_byCDView(flag) {
                let data = [
                    {genre: 'Sports', sold: 275},
                    {genre: 'Strategy', sold: 115},
                    {genre: 'Action', sold: 120},
                    {genre: 'Shooter', sold: 350},
                    {genre: 'Other', sold: 150}
                ];
                let data1 = [
                    {genre: 'Sports', sold: 200},
                    {genre: 'Strategy', sold: 123},
                    {genre: 'Action', sold: 234},
                    {genre: 'Shooter', sold: 232},
                    {genre: 'Other', sold: 111}
                ];
                let config = {
                    VC: {  //(visual channel)
                        position: ['genre', 'sold'], //x->genre .y->sold
                        color: 'genre' //->genre
                    },
                    tooltip: {
                        content: function (d) {
                            return `genre: ${d.genre},sold: ${d.sold}`
                        }
                    },
                    legend: {
                        content: function (d) {
                            return d.genre
                        },
                        position: 'top'
                    },
                    axis: {
                        yRange: [0, 500]
                    }
                };
                if (flag === 0) {
                    this.update = this.$cdview.basic_bar.selectById('base_bar_c').draw(data, config);
                } else if (flag === 1) {
                    this.update.reDraw(data1);
                } else {
                    this.update.reDraw(data);
                }
            },
            draw_byCDView1() {
                let data = [
                    {
                        'year': 2016,
                        'quarters': [{'quarter': '', sales: 500}, {'quarter': '', sales: 343}, {
                            'quarter': '',
                            sales: 234
                        }, {'quarter': '', sales: 560}]
                    },
                    {
                        'year': 2017,
                        'quarters': [{'quarter': '', sales: 400}, {'quarter': '', sales: 222}, {
                            'quarter': '',
                            sales: 600
                        }, {'quarter': '', sales: 322}]
                    },
                    {
                        'year': 2018,
                        'quarters': [{'quarter': '', sales: 600}, {'quarter': '', sales: 401}, {
                            'quarter': '',
                            sales: 460
                        }, {'quarter': '', sales: 600}]
                    }
                ];
                let config = {
                    VC: {
                        groupName: 'year',
                        members: 'quarters',
                        memberName: 'quarter',
                        memberValue: 'sales',
                        color: 'quarter'
                    },
                    tooltip: {
                        content: function (d) {
                            return `${d.quarter}${d.sales}`
                        }
                    },
                    legend: {
                        content: function (d) {
                            return `${d.quarter}`
                        },
                        position: 'top'
                    }
                };
                this.$cdview.group_bar.selectById('group_bar').draw(data, config)
            },
            drawPayoff() {
                let data = [
                    {date: '2018/12/01', income: 100, pay: 30},
                    {date: '2018/12/02', income: 230, pay: 90},
                    {date: '2018/12/03', income: 140, pay: 100},
                    {date: '2018/12/04', income: 80, pay: 200},
                    {date: '2018/12/05', income: 200, pay: 110},
                    {date: '2018/12/06', income: 20, pay: 130},
                    {date: '2018/12/07', income: 200, pay: 100},
                ];
                let config={
                    VC: {
                        plus: 'income',
                        minus: 'pay',
                        date: 'date'
                    },
                    tooltip: {
                        content: function (d) {
                            return `${d.pay},${d.income},${d.income-d.pay}`
                        }
                    },
                    axis: {
                        yTickFormat: function (timeFormat) { //y
                            return timeFormat('%m%d')
                        }
                    },
                    legend: {
                        content: function(d){
                          return d.id===0?'':d.id===1?'':d.id===2?'':''
                        },
                        position: 'top'
                    }
                };
                this.$cdview.plusMinusBar.selectById('payoff_bar').draw(data, config);
            }
        },
        mounted() {
            this.draw_byCDView(0);
            this.draw_byCDView1();
            this.drawPayoff();
        }
    }
</script>

