import util from '../util/base_util.js'

let d3 = require('d3');
let selectById = function (id) {
    //
    let model = util.getContainerModel(id);
    //svg
    let addSvg = util.selectContainerAddSvg()
        .id(id)
        .model(model);
    let svg = addSvg();
    let gW = model.gW;
    let gH = model.gH;
    let tx = model.tx;
    let ty = model.ty;
    return {
        draw: function (data, config) {
            //
            function getTree() {
                /*
                hierarchynodeTree
                */
                let newData = d3.hierarchy(data, function (d) { //root=newData
                    return !config.VC?d.children:d[config.VC.children] || d.children //children
                });
                //valuecountsortsumvalue
                newData.count();//value
                /*
                * 
                * */
                //
                let tree;
                //
                if (config&&'style' in config&&config.style==='ordinary'){
                    tree = d3.tree()
                        .size([model.gW, model.gH]);//
                } else {
                    tree = d3.cluster()
                        .size([model.gW, model.gH]);//
                }
                tree(newData);//x,ylinks
                return newData
            }
            let newData = getTree();
            //link
            let link=d3.linkHorizontal() //source d.source,
                .x(function(d) { return d.y; }) //xy
                .y(function(d) { return d.x; });
            /*
            * 
            * */
            //
            let g = svg.append('g')
                .attr("font-family", "sans-serif")
                .attr("font-size", (config.size||10))
                .attr('transform', 'translate(' + tx + ',' + ty + ')');
            //
            //console.log(newData.links());
            const links = g.append("g")
                .attr("fill", "none")
                .attr("stroke", "#555")
                .attr("stroke-opacity", 0.4)
                .attr("stroke-width", 1.5)
                .selectAll("path")
                .data(newData.links()) //links[{source: Node,target: Node},{}]
                .enter().append("path")
                .attr("d", link);//svgd3newDataroot
            //
            //console.log(newData.descendants());
            //g
            const nodes = g.append("g")
                .attr("stroke-linejoin", "round")
                .attr("stroke-width", 3)
                .selectAll("g")
                .data(newData.descendants().reverse())//reverse
                .enter().append("g")
                .attr("transform", d => `translate(${d.y},${d.x})`);
            //
            nodes.append("circle")
                .attr("fill", d => d.children ? "#555" : "#999")
                .attr("r", config.nodeRadius||2.5);
            //
            nodes.append("text")
                .attr("dy", "0.31em")
                .attr("x", d => d.children ? -6 : 6) //
                .text(d => !config.VC?d.data.name:d.data[config.VC.nodeName]||d.data.name)
                .filter(d => d.children) //text
                .attr("text-anchor", "end")
                .clone(true).lower()     //g,lower: 
                .attr("stroke", "white");
        }
    }
};
export default {
    selectById
}