import util from '../util/base_util.js'

let d3 = require('d3');
import d3_sankey from 'sankey'//d3sankey
let selectById=function (id) {
    //svg
    let d3Id = "#" + id;
    let svg = d3.select(d3Id).select('svg');
    //,g
    let model = util.getContainerModel(id);
    var height = model.height;
    var width = model.width;
    let tx = model.tx;
    let ty = model.ty;
    //g
    let g = svg.append('g').attr('transform', 'translate(' + tx + ',' + ty + ')');
    return {
        draw(data){
            // 
            console.log(d3_sankey)
            let sankey = d3_sankey()
                .nodeWidth(80)
                .nodePadding(40)
                .size([width, height])
                .nodes(data.nodes)
                .links(data.links)
                .layout(3);
            // 
            var path = sankey.link();
            // 
            var links = svg.append("g").selectAll("path")
                .data(data.links)
                .enter()

// 
            var nodes = svg.append("g").selectAll(".node")
                .data(data.nodes)
                .enter();

            let color=d3.scale.category20()
// 
            links.append("path")
                .attr({
                    fill: "none",   //
                    stroke: function(d,i){ return color(i); },  //
                    "stroke-opacity": 0.5,  //
                    d: path,  //
                    id: function(d,i){ return 'link' +i }  //ID
                })
                .style("stroke-width", function (d) {  //
                    return Math.max(1, d.dy);
                });

// 
            nodes.append("circle")
                .attr("transform",function (d) {
                    return "translate(" + d.x + "," + d.y + ")";
                })
                .attr("r", function(d) { return d.dy / 2; })
                .attr("cx", function(d) { return d.dx/2; })
                .attr("cy", function(d) { return d.dy / 2; })
                .style("fill", "tomato")
                .style("stroke", "gray");

        }
    }
}
export default {
    selectById
}