import util from '../util/base_util.js'

let d3 = require('d3');
let selectById = function (id) {
    //
    let model = util.getContainerModel(id);
    //svg
    let addSvg = util.selectContainerAddSvg()
        .id(id)
        .model(model);
    let svg = addSvg();
    return {
        draw: function (data, config) {
            //d3hierarchychildrenchildren
            let dataString = JSON.stringify(data);
            let hierarchy = config.VC.hierarchy;
            hierarchy.forEach(item => {
                let reg = '\\b' + item + '\\b';
                dataString = dataString.replace(new RegExp(reg, 'g'), 'children');
            });
            data = JSON.parse(dataString);
            // console.log(data);

            //,g
            let tx = model.tx;
            let ty = model.ty;
            let gW = model.gW;
            let gH = model.gH;
            //g
            let g = svg.append('g').attr('transform', 'translate(' + tx + ',' + ty + ')');

            //d3pack
            let newData = d3.hierarchy(data, function (d) { //root=newData
                return !config.VC?d.children:d[config.VC.children] || d.children //children
            });
            //valuecountsortsumvalue
            //

            newData.sum(function () {
                return 1;
            });//
            let getPackData = d3.pack()
                .size([gW, gH]);  //
            //{name,children: [{}]}
            //packnamechildren
            //children[{},{}....]
            //packjsonnodexcxycyrsizeradiusvalue
            //sizeradiusvaluevalue
            //node/packArea
            let nodes = getPackData(newData).descendants();
            let circle = g.selectAll('circle')
                .data(nodes)
                .enter()
                .append("circle")
                .attr("fill", "rgb(31, 119, 180)")
                .attr("fill-opacity", "0.4")
                .attr("cx", function (d) {
                    return d.x;
                })
                .attr("cy", function (d) {
                    return d.y;
                })
                .attr("r", function (d) {
                    return d.r;
                });
            //
            if (config && 'interactive' in config && config.interactive === false) {
                // doNothing
            } else {
                circle.on('mouseover', function () {
                    d3.select(this).attr('fill', 'yellow')
                })
                    .on('mouseout', function () {
                        d3.select(this).attr('fill', 'rgb(31, 119, 180)')
                    })
            }
            //
            let maxDepth = d3.max(nodes, function (d) {
                return d.depth
            });
            if (config && 'text' in config && config.text === true) {
                g.selectAll('text')
                    .data(nodes)
                    .enter()
                    .append('text')
                    .attr('x', function (d) {
                        return d.x
                    })
                    .attr('y', function (d) {
                        return d.y
                    })
                    .text(function (d) {
                        return d.data.name
                    })
                    .attr('fill-opacity', function (d) {
                        if (d.depth === maxDepth) {  //
                            return 0.9
                        } else {
                            return 0
                        }
                    })
                    .attr('fill', '#ffffff')
                    .attr('font-size', gH / maxDepth / 30)
                    .attr('text-anchor', 'middle')

            }
        }
    }
};
export default {
    selectById
}