                        size: 'value',   (->value)
                        color: 'name'    (->name)
                    },
                    2pie: {
                        innerRadius: 50,  (0)
                        outerRadius: 180  ()
                    },
                    3text: {
                        content: function (d) {   (d)
                            return `xxx`          ()
                        },
                        fontSize: function (d) {  ()
                            return d.data.value/3
                        }
                    },
                    4interactive: true         ()
                    5lineTip: {                (false)
                        lineColor: '#000'        ()
                    }
