<script>
    export default {
        name: "ringArea",
        methods: {
            drawRingByCDView() {
                let data = [{name: '', value: 100}, {name: '', value: 80}, {name: '', value: 150}];
                let config = {
                    VC: {
                        size: 'value',
                        color: 'name'
                    },
                    pie: {
                        innerRadius: 50,
                        outerRadius: 180
                    },
                    text: {
                        content: function (d) {
                            return `${d.data.name}/${d.data.value}`
                        },
                        fontSize: 20
                    },
                    tooltip: {
                        content: function (d) {
                            //console.log(d) d
                            return (d.data.name + '' + d.value)
                        }
                    },
                    legend: {
                        content: function (d) {
                            return `${d.name}`
                        }
                    },
                    interactive: false
                };
                this.$cdview.ringArea.selectById('ringArea_c').draw(data, config)
            },
            drawNRByC(flag) {
                let data = [
                    {
                        name: '',
                        value: 60.8
                    }, {
                        name: '',
                        value: 30.8
                    }, {
                        name: '',
                        value: 30.4
                    }, {
                        name: '',
                        value: 40.8
                    }, {
                        name: '',
                        value: 70.8
                    }, {
                        name: '',
                        value: 20.8
                    }];
                let data1 = [
                    {
                        name: '',
                        value: 23
                    }, {
                        name: '',
                        value: 56
                    }, {
                        name: '',
                        value: 77
                    }, {
                        name: '',
                        value: 34
                    }, {
                        name: '',
                        value: 10
                    }, {
                        name: '',
                        value: 66
                    }];
                let config = {
                    VC: {
                        color: 'name',
                        size: 'value'
                    },
                    pie: {
                        innerRadius: 0,
                        outerRadius: 130,
                    },
                    tooltip: {
                        content: function (d) {
                            //console.log(d) d
                            return (d.data.name + '' + d.value + '')
                        }
                    },
                    legend: {
                        content: function (d) {
                            return `${d.name}`
                        }
                    },
                    text: {
                        content: function (d) {
                            return d.data.name
                        },
                        fontSize: function (d) {
                            return d.data.value/3
                        }
                    },
                    lineTip: {
                        lineColor: '#ff050c'
                    }
                };
                if (flag===0) {
                    this.update=this.$cdview.nightingale_rose.selectById('nightingale_rose_c').draw(data, config)
                } else if(flag===1){
                    this.update.reDraw(data1);
                } else {
                    this.update.reDraw(data);
                }
            }
        },
        mounted() {
            this.drawRingByCDView();
            this.drawNRByC(0)
        }
    }
</script>

