<template>
    <div>
        <el-row>
            <el-col :span="16">
                <div id="base_dispersedPoint_c" class="dv_box" :style="{width: '500px', height: '500px'}"></div>
            </el-col>
            <el-col :span="8">
                <div class="dv_description">
                    <p>
                        
                    </p>
                    <p class="dv_p">
                        
                        x
                        yy
                    </p>
                    <el-button @click="drawByCDview(1)"></el-button>
                    <el-button @click="drawByCDview(2)"></el-button>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="16">
                <div id="base_dispersedPoint1_c" class="dv_box" :style="{width: '500px', height: '500px'}"></div>
            </el-col>
            <el-col :span="8">
                <div class="dv_description">
                    <p>
                        
                    </p>
                    <p class="dv_p">
                        multi
                    </p>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <div class="dv_description">
                <pre class="dv_pre">

                    basic_dispersedPoint
                    
                    1VC: {
                        position: ['population', 'product'], (x->y->)
                        color: 'name',                       (->)
                        size: 'product'                      (->)
                    },
                    2multi: true                           (boolean,)
                </pre>
            </div>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: "Basic_dispersedPoint",
        methods: {
            drawByCDview(flag) {
                let data = [
                    {name: '', population: 6000, product: 2820},
                    {name: '', population: 5132, product: 2401},
                    {name: '', population: 4000, product: 1622},
                    {name: '', population: 3850, product: 891},
                    {name: '', population: 2001, product: 666}
                ];
                let data1 = [
                    {name: '', population: 6000, product: 1500},
                    {name: '', population: 5132, product: 3000},
                    {name: '', population: 4000, product: 2000},
                    {name: '', population: 3850, product: 6000},
                    {name: '', population: 2001, product: 5000}
                ];
                let config = {
                    VC: {
                        position: ['population', 'product'],
                        color: 'name',
                        size: 'product'
                    },
                    tooltip: {
                        content: function (d) {
                            // console.log(d) //d
                            return (`${d.name}: ${d.population}: ${d.product}`)
                        }
                    },
                    legend: {
                        content: function (d) {
                            return `${d.name}`
                        }
                    },
                    supLine: {
                        content: '',
                        position: {
                            start: [2100, 1600],
                            end: [6000, 1600]
                        }
                    },
                    axis:{
                        yRange: [0,8000],
                        xRange: [0,7000]
                    }
                };
                if (flag===0) {
                    this.update=this.$cdview.basic_dispersedPoint.selectById('base_dispersedPoint_c').draw(data, config);
                } else if(flag===1){
                    this.update.reDraw(data1);
                } else {
                    this.update.reDraw(data);
                }
            },
            drawByCDview1() {
                let dataset = [
                    {name: '', population: 6000, product: 2820},
                    {name: '', population: 5132, product: 2401},
                    {name: '', population: 4000, product: 1622},
                    {name: '', population: 3850, product: 891},
                    {name: '', population: 2001, product: 666}
                ];
                let config1 = {
                    VC: {
                        position: ['population', 'product'],
                        color: 'name',
                        size: 'product'
                    },
                    multi: true,
                    tooltip: {
                        content: function (d) {
                            // console.log(d) //d
                            return (`${d.name}: ${d.population}: ${d.product}`)
                        }
                    },
                    legend: {
                        content: function (d) {
                            return `${d.name}`
                        }
                    },
                    supLine: {
                        content: 'dshdsd sd',
                        position: {
                            start: [2100, 1600],
                            end: [6000, 1600]
                        }
                    }
                };
                this.$cdview.basic_dispersedPoint.selectById('base_dispersedPoint1_c').draw(dataset, config1);
            }
        },
        mounted() {
            this.drawByCDview(0);
            this.drawByCDview1();
        }
    }
</script>

