<template>
    <div>
        <el-row>
            <el-col :span="16">
                <div id="chinaMap_c" class="dv_box" :style="{width: '500px', height: '500px'}"></div>
            </el-col>
            <el-col :span="8">
                <div class="dv_description">
                    <p>
                        
                    </p>
                    <p class="dv_p">
                    </p>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="16">
                <div id="usaMap_c" class="dv_box" :style="{width: '500px', height: '500px'}"></div>
            </el-col>
            <el-col :span="8">
                <div class="dv_description">
                    <p>
                        
                    </p>
                    <p class="dv_p">
                        http://mapshaper.org/ 
                    </p>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <div class="dv_description">
                <pre class="dv_pre">

                    basic_map
                    
                      1geometry: 'polygon', //
                      2size: 0.9,          (,1)
                      3text: true          ()
                      4map: {
                              color: 'white'     ()
                              stroke: '#000'     ()
                              fill_opacity: 0.5  (0-1)
                         }
                      5dots: [             (value)
                                {name: '',value: 100},
                                {name: '',value: 80},
                                {name: '',value: 61},
                                {name: '',value: 40},
                                {name: '',value: 66}
                            ],
                      6dotsTooltip: {      (tooltip)
                                content: function (d) {
                                    return `${d.dots.name}xx`
                                }
                            }
                </pre>
            </div>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: "basic_map",
        methods: {
            drawByC() {
                this.$axios.get('/api/china_simplify')
                    .then(res => {
                        let config = {
                            map: {
                                // fill: 'white',
                                // stroke: '#000',
                                 fill_opacity: 0.6
                            },
                            geometry: 'polygon', //
                            size: 0.9,
                            tooltip: {
                                content: function (d) {
                                    return `${d.properties.name}`
                                }
                            },
                            dots: [
                                {name: '',value: 100},
                                {name: '',value: 80},
                                {name: '',value: 61},
                                {name: '',value: 40},
                                {name: '',value: 66}
                            ],
                            dotsTooltip: {
                                content: function (d) {
                                    return `${d.dots.name}xx`
                                }
                            }
                        };
                        this.$cdview.basic_map.selectById('chinaMap_c').draw(res.data, config)
                    })
            },
            drawByC2() {
                this.$axios.get('/api/usa_geo_simplify')
                    .then(res => {
                        let config = {
                            geometry: 'multiPolygon',
                            size: 1.2
                        };
                        this.$cdview.basic_map.selectById('usaMap_c').draw(res.data, config)
                    })
            }

        },
        mounted() {
            this.drawByC();
            this.drawByC2()
        }
    }
</script>

