/*
*   1id: id
*         2container 
* */
function selectContainerAddSvg() {
    let _id;
    let _model;

    function add() {
        let container = d3.select("#" + _id);
        let height = _model.height;
        let width = _model.width;
        let _svg = container.append('svg').attr('width', width).attr('height', height);
        return _svg
    }

    add.id = function (id) {
        _id = id;
        return add
    };
    add.model = function (model) {
        _model = model;
        return add
    };
    return add
}

export default {
    getContainerModel,
    array2xy,
    stringST2ObjectST,
    setMultiText,
    setToolTip,
    setSupLine,
    setLegend,
    setAxis,
    selectContainerAddSvg,
    setPolarAxis
}