import util from '../util/base_util.js'

let d3 = require('d3');
let selectById = function (id) {
    //
    let model = util.getContainerModel(id);
    //svg
    let addSvg = util.selectContainerAddSvg()
        .id(id)
        .model(model);
    let svg = addSvg();
    //,g
    let tx = model.tx;
    let ty = model.ty;
    let gW = model.gW;
    let gH = model.gH;
    return {
        draw: function (data, config) {
            //d3hierarchychildrenchildren
            let dataString=JSON.stringify(data);
            let hierarchy=config.VC.hierarchy;
            hierarchy.forEach(item=>{
                let reg='\\b'+item+'\\b';
                dataString= dataString.replace(new RegExp(reg,'g'),'children');
            });
            data=JSON.parse(dataString);
            //treeMap
            function treeMap() {
                /*
                hierarchynodeTree
                */
                let newData = d3.hierarchy(data, function (d) { //root=newData
                    return !config.VC?d.children:d[config.VC.children] || d.children //children
                });
                //valuecountsortsumvalue
                //
                let value=config.VC.size;
                newData.sum(function (d) {
                    return d[value];
                });//
                /*
                * ,
                * */
                //
                //valuexydxdy
                let getTreeMap = d3.treemap()
                    .size([gW, gH])
                    .padding(1)
                    .round(true);//
                 getTreeMap(newData);
                 return newData;
            }
            data=treeMap();
            let getColor = d3.scaleOrdinal(d3.schemeCategory10);
            //console.log(data);
            //g
            let g = svg.append('g').attr('transform', 'translate(' + tx + ',' + ty + ')');
            let gGroups = g.selectAll('g.rectArea')
                .data(data.leaves()) //
                .enter()
                .append('g')
                .attr('class', 'rectArea')
                .attr('transform', function (d) {
                    return 'translate(' + d.x0 + ',' + d.y0 + ')'
                });
            gGroups.append('rect')
                .attr('width', function (d) {
                    return d.x1-d.x0
                })
                .attr('height', function (d) {
                    return d.y1-d.y0
                })
                .attr('fill', function (d) {
                    return getColor(d.parent.data.name)//
                });
            //
            if (config && 'text' in config) {
                gGroups.append('text')
                    .style('font-size',config.text.fontSize)
                    .attr('dy','0.5em')
                    .text(config.text.content)
            }
        }
    }
};
export default {
    selectById
}