import util from '../util/base_util.js'

let selectById = function (id) {
    //
    let model = util.getContainerModel(id);
    //svg
    let addSvg = util.selectContainerAddSvg()
        .id(id)
        .model(model);
    let svg = addSvg();
    return {
        draw(data, config) {
            //d3hierarchychildrenchildren
            let dataString=JSON.stringify(data);
            let hierarchy=config.VC.hierarchy;
            hierarchy.forEach(item=>{
                let reg='\\b'+item+'\\b';
                dataString= dataString.replace(new RegExp(reg,'g'),'children');
            });
            data=JSON.parse(dataString);
            //,g
            let gW = model.gW;
            let width = model.width;
            let height = model.height;
            //g
            //g0,0
            let g = svg.append('g').attr('transform', 'translate(' + width / 2 + ',' + height / 2 + ')');
            //d3pack
            let newData1 = d3.hierarchy(data, function (d) { //root=newData
                return !config.VC?d.children:d[config.VC.children] || d.children //children
            });
            //valuecountsortsumvalue
            //

            newData1.count();//

            //
            let radius = gW / 2;
            let partitionData = d3.partition()
                .padding(0)
                .size([2 * Math.PI, radius]);  //
                //size  2 * PI 

            //
            let newData = partitionData(newData1).descendants(); //partitionData(data),descendants: 
            console.log(newData)
            /*
                        :
                        x: 
                        y: 
                        dx: 
                        dy: 
                        depth: ()
                        value: 
            */
            let getColor = d3.scaleOrdinal(d3.schemeCategory10);
            //
            let getArcPath = d3.arc()
                .startAngle(function (d) {
                    return d.x0 + 0.01
                })
                .endAngle(function (d) {
                    return  d.x1
                })
                .innerRadius(function (d) {
                    return d.y0 + 2
                })
                .outerRadius(function (d) {
                    return (d.y1)
                });

            //
            let arcGs = g.selectAll('g.arcPartition')
                .data(newData)
                .enter()
                .append('g')
                .attr('class', 'rectPartition');
            arcGs.append('path')
                .attr('d', getArcPath)
                .attr('fill', function (d, i) {
                    if (i === 0) {
                        return 'none'
                    }
                    return getColor((d.children ? d : d.parent).data.name)
                });
            //
            if (config && 'text' in config) {
               let add= util.setMultiText()
                    .rowMaxWidth(function () {
                        return 100
                    })
                    .fontSize(function (d) {
                        return  (30 / (d.depth + 1))
                    })
                    .posX(function (d) {
                        return getArcPath.centroid(d)[0];
                    })
                    .posY(function (d) {
                        return getArcPath.centroid(d)[1]
                    })
                    .content(config.text.content);
                add(arcGs);
                d3.select('.multiText')
                    .attr('x',0)
                    .attr('y',0)
                    .select('tspan')
                    .attr('dy','0.2em')
            }

        }
    }
};
let d3 = require('d3');
export default {
    selectById
}