import util from '../util/base_util.js'
import colorUtil from "../util/colorUtil";

let d3 = require('d3');
let selectById = function (id) {
    //
    let model = util.getContainerModel(id);
    //svg
    let addSvg = util.selectContainerAddSvg()
        .id(id)
        .model(model);
    let svg = addSvg();
    return {
        draw: function (data, config) {
            //,g
            let width = model.width;
            let height = model.height;
            //g
            //g0,0
            let g = svg.append('g').attr('transform', 'translate(' + width / 2 + ',' + height / 2 + ')');
            //[{name: '',value: 100},{}]
            //total.value=valued.angle=d.value/total.value*2pi
            //    d3pie
            let getPieData = d3.pie()
                .sort(function (d, i) {//
                    return i
                })
                .value(function (d) {
                    return d[config.VC.size]
                });
            //    pieppsortf
            //    pf

            //    
            let arcG = d3.arc()
                .innerRadius(config.pie.innerRadius)//
                .outerRadius(config.pie.outerRadius);

            let getColor = d3.scaleOrdinal().range(colorUtil.pieColors);
            //    
            let slices = g.selectAll('path.arc')
                .data(getPieData(data));//[{startAngle:0,endAngle: 0.1*2pi},{}]
            //    enter
            let arcs = slices.enter()
                .append('path')
                .attr('d', function (d) {
                    return arcG(d)
                })
                .attr('class', 'arc')
                .attr('fill', function (d) {
                    return getColor(d.data[config.VC.color])
                });

            //
            if (config && 'text' in config) {
                let add = util.setMultiText()
                    .content(
                        config.text.content
                    )
                    .posX(function (d) {
                        return arcG.centroid(d)[0]
                    })
                    .posY(function (d) {
                        return arcG.centroid(d)[1]
                    })
                    .rowMaxWidth(function () {
                        return 100
                    })
                    .fontSize(config.text.fontSize)
                    .graphSelection(arcs);
                add();
            }
            //   tooltip
            if (config && 'tooltip' in config && config.tooltip) {
                let addToolTip = util.setToolTip(arcs)
                    .content(config.tooltip.content);
                addToolTip()
            }
            //legend
            if ('legend' in config) {
                let addLegend = util.setLegend()
                    .svg(svg)
                    .data(data)
                    .useColor(function (color, config, d) {
                        return color(d[config.VC.color])
                    })
                    .color(getColor)
                    .content(config.legend.content)
                    .config(config)
                    .model(model);
                addLegend()
            }
            //
            if (config && 'interactive' in config && config.interactive === false) {
                //do nothing
            } else {
                arcs.on('mouseover.bounce', function () {
                    d3.select(this)
                        .style('opacity', 0.7)
                        .transition()
                        .duration(200)
                        .ease('bounce')//
                        .attr('d', function (d) {
                            let arc = d3.svg.arc()
                                .innerRadius(config.pie.innerRadius) // ,0
                                .outerRadius(config.pie.outerRadius * 1.2); // 
                            return arc(d)
                        })
                });
                arcs.on('mouseout.bounce', function () {
                    d3.select(this)
                        .transition()
                        .duration(200)
                        .ease('bounce')//
                        .attr('d', function (d) {
                            return arcG(d)
                        })
                        .style('opacity', 1)
                })
            }
        }
    }
};
export default {
    selectById
}