<template>
    <div>
    <el-row>
        <el-col :span="16">
            <div id="radar_c" class="dv_box" :style="{width: '500px', height: '500px'}"></div>
        </el-col>
        <el-col :span="8">
            <div class="dv_description">
                <p>
                    
                </p>
                <p class="dv_p">
                     
                    
                    
                    
                </p>
                <el-button @click="draw(1)"></el-button>
                <el-button @click="draw(2)"></el-button>
            </div>
        </el-col>
    </el-row>
    <el-row>
        <div class="dv_description">
                <pre class="dv_pre">

                    radar
                    
                     1VC: {
                        attr: 'attribute',   (->)
                        attrName: 'name',    (->)
                        attrValue: 'value',  (->)
                        color: 'name'        (->)
                    },
                    2rangeValue: [0,100],   ()
                    3interval: 10,          ()
                    4interactive: boolean   ()
                </pre>
        </div>
    </el-row>
    </div>
</template>

<script>
    export default {
        name: "simple_radar",
        methods: {
            draw(flag) {
                let data = [
                    {
                        name: '',
                        attribute: [{name: '', value: 66}, {name: '', value: 20}, {
                            name: '',
                            value: 88
                        }, {name: '', value: 68}, {name: '', value: 30}]
                    },
                    {
                        name: '',
                        attribute: [{name: '', value: 15}, {name: '', value: 90}, {
                            name: '',
                            value: 60
                        }, {name: '', value: 50}, {name: '', value: 76}]
                    },
                    {
                        name: '',
                        attribute: [{name: '', value: 92}, {name: '', value: 40}, {
                            name: '',
                            value: 20
                        }, {name: '', value: 16}, {name: '', value: 73}]
                    }
                ];
                let data1 = [
                    {
                        name: '',
                        attribute: [{name: '', value: 44}, {name: '', value: 30}, {
                            name: '',
                            value: 88
                        }, {name: '', value: 55}, {name: '', value: 56}]
                    },
                    {
                        name: '',
                        attribute: [{name: '', value: 34}, {name: '', value: 34}, {
                            name: '',
                            value: 67
                        }, {name: '', value: 13}, {name: '', value: 56}]
                    },
                    {
                        name: '',
                        attribute: [{name: '', value: 54}, {name: '', value: 66}, {
                            name: '',
                            value: 55
                        }, {name: '', value: 11}, {name: '', value: 77}]
                    }
                ];
                let config = {
                    VC: {
                        attr: 'attribute',
                        attrName: 'name',
                        attrValue: 'value',
                        color: 'name'
                    },
                    rangeValue: [0,100],
                    interval: 10,
                    tooltip: {
                        content: function (d) {
                            return `${d.name}<br>
                                    ${d.attribute[0].value}<br>
                                    ${d.attribute[1].value}<br>
                                    ${d.attribute[2].value}<br>
                                    ${d.attribute[3].value}<br>
                                    ${d.attribute[4].value}
                                    `
                        }
                    },
                    legend:{
                        content: function (d) {
                            return `${d.name}`
                        },
                        position: 'top'
                    }
                };
                if (flag===0) {
                    this.update=this.$cdview.radar.selectById('radar_c').draw(data,config)
                } else if(flag===1){
                    this.update.reDraw(data1);
                } else {
                    this.update.reDraw(data);
                }
            }
        },
        mounted() {
            this.draw(0)
        }
    }
</script>

