<template>
    <div>
        <el-row>
            <el-col :span="16">
                <div id="triangle_c" class="dv_box" :style="{width: '500px', height: '500px'}"></div>
            </el-col>
            <el-col :span="8">
                <div class="dv_description">
                    <p>
                        
                    </p>
                    <p class="dv_p">
                        
                        
                        
                    </p>
                    <el-button @click="drawTriangleByC(1)"></el-button>
                    <el-button @click="drawTriangleByC(2)"></el-button>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <div class="dv_description">
                <pre class="dv_pre">

                    inverted_triangle
                    
                    1VC: {
                          sort: 'salary',  ()
                          color: 'name'    ()
                    }
                </pre>
            </div>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: "inverted_triangle",
        methods:{
            drawTriangleByC(flag) {
                let data = [
                    {name: '', salary: 10000},
                    {name: '', salary: 8000},
                    {name: '', salary: 2000},
                    {name: '', salary: 9000}
                ];
                let data1 = [
                    {name: '', salary: 2000},
                    {name: '', salary: 10000},
                    {name: '', salary: 8000},
                    {name: '', salary: 6000}
                ];
                let config={
                    VC: {
                        sort: 'salary',
                        color: 'name'
                    },
                    legend: {
                        content :function (d) {
                            return `${d.data.name}`
                        }
                    },
                    tooltip: {
                        content: function (d) {
                           return (`${d.data.name}${d.data.salary}`)
                        }
                    },
                    text: {
                        content: function (d) {
                            return d.data.name+''
                        }
                    }
                };
                if (flag===0) {
                    this.update=this.$cdview.inverted_triangle.selectById('triangle_c').draw(data,config);
                } else if(flag===1){
                    this.update.reDraw(data1);
                } else {
                    this.update.reDraw(data);
                }
            }
        },
        mounted(){
            this.drawTriangleByC(0)
        }
    }
</script>

